`, "",{"version":3,"sources":["webpack://./src/components/Categories.vue"],"names":[],"mappings":";AA8KA;IACA,eAAA;IACA,kBAAA;IACA,iBAAA;IACA,oBAAA;AACA;AACA;IACA,YAAA;IACA,WAAA;IACA,sBAAA;AACA;AACA;IACA,YAAA;IACA,kBAAA;IACA,WAAA;AACA;AACA;IACA,eAAA;IACA,kBAAA;IACA,kBAAA;AACA;AAEA;IACA,kBAAA;IACA,kBAAA;IACA,eAAA;AACA;AACA;IACA,cAAA;IACA,kBAAA;AACA;AACA;IACA,kBAAA;IACA,WAAA;IACA,qBAAA;AAEA;AACA;IACA,eAAA;IACA,cAAA;IACA,qBAAA;IACA,qCAAA;IACA,qBAAA;AACA;AACA;IACA,WAAA;IACA,YAAA;IACA,kBAAA;IACA,qBAAA;IACA,gBAAA,EAAA,gCAAA;AACA;AAEA;IACA,kCAAA;AACA;AACA;IACA,qBAAA;AACA;AACA;IACA,yBAAA;IACA,WAAA;IACA,YAAA;AACA;AACA;IACA,2BAAA;IACA,gBAAA;IACA,UAAA;IACA,eAAA;AACA;AACA;IACA,iBAAA;IACA,mBAAA;IACA,cAAA;IACA,kBAAA;AACA;AACA;IACA,UAAA;IACA,aAAA;IACA,cAAA;AACA;AACA;IACA,UAAA;AACA;AACA;;;;;GAKA","sourcesContent":["<template>\r\n    <div id=\"main-panel\">\r\n        <div id=\"Add-panel\">\r\n        <h2>Add Category</h2>\r\n            <form @submit.prevent=\"addCategories\">\r\n                Category Name:&nbsp&nbsp<input v-model=\"categoryData.name\" type=\"text\" placeholder=\"Category name\" id=\"cat-name\"><br>\r\n                Choose a color:&nbsp&nbsp<input v-model=\"categoryData.color\" type=\"color\" id=\"cat-color\" value=\"fafafa\"><br>\r\n                Auto:&nbsp&nbsp<input v-model=\"categoryData.auto\" type=\"checkbox\" id=\"autoCheckBox\"><br>\r\n                <button type=\"submit\" id=\"category-added\">Add Category</button>\r\n            </form>\r\n        </div>\r\n        <div id=\"Info-Panel\" class=\"div-child\">\r\n            <h2>Firewall Categories:</h2>\r\n            <div id=\"table-container\" class=\"d-flex justify-content-center align-items-center\">\r\n                <btable v-if=\"categories.length > 0\" id=\"table-content\">\r\n                    <thead>\r\n                        <tr>\r\n                            <th @click=\"sortBy('name')\">Name</th>\r\n                            <th @click=\"sortBy('auto')\">Auto</th>\r\n                            <th @click=\"sortBy('color')\">Color</th>\r\n                            <th>Action</th>\r\n                        </tr>\r\n                    </thead>\r\n                    <tbody>\r\n                        <tr v-for = \"category in sortedCategories\" :key=\"category.uuid\">\r\n                            <td>{{ category.name }}</td>\r\n                            <td>\r\n                                <div v-if=\"category.auto === '1' \" class=\"circle green\"></div>\r\n                                <div v-else-if=\"category.auto === '0'\" class=\"circle red\"></div>\r\n                            </td>\r\n                            <td>\r\n                                <div :style=\"{ backgroundColor: '#'+category.color }\" class=\"color-box\"></div>\r\n                            </td>\r\n                            <td>\r\n                                <button @click=\"deletecategory(category.uuid)\" id=\"delete-cat\">Delete</button>\r\n                            </td>\r\n                        </tr>\r\n                    </tbody>\r\n                </btable>\r\n                <p v-else>No categories found.</p>\r\n            </div>\r\n        </div>\r\n        <!-- Displaying messages -->\r\n        <Popup ref=\"Popup\" :message=\"message\"  />\r\n    </div>\r\n    \r\n</template>\r\n\r\n<script>\r\nimport axios from '@nextcloud/axios';\r\nimport { generateUrl } from '@nextcloud/router';\r\nimport Popup from './Popup.vue'\r\n\r\nexport default {\r\n    components: {\r\n      Popup, // Register the Popup component\r\n    },\r\n    data() {\r\n        return {\r\n            categoryData: {\r\n                auto: \"\",\r\n                color: \"\",\r\n                name: \"\",\r\n            },\r\n            message: \"\",\r\n            updateKey: 0,\r\n            categories: [],\r\n            sortKey: 'name', // Property to store the current sorting key\r\n            sortDirection: 1, // 1 for ascending, -1 for descending\r\n        };\r\n    },\r\n    created() {\r\n        this.fetchCategories();\r\n    },\r\n    computed: {\r\n      sortedCategories() {\r\n        return this.categories.slice().sort((a, b) => {\r\n          const modifier = this.sortDirection === 1 ? 1 : -1;\r\n          return modifier * a[this.sortKey].localeCompare(b[this.sortKey]);\r\n        });\r\n      },\r\n    },\r\n    watch: {\r\n        color(value) {\r\n            if (value.toString().match(/#[a-zA-Z0-9]{8}/)) {\r\n                this.color = value.substr(0, 7);\r\n            }\r\n        },\r\n        updateKey: function(newMessage) {\r\n        if (newMessage) {\r\n          this.$refs.Popup.openPopup(); // Trigger the popup\r\n        }\r\n      },\r\n    },\r\n    methods: {\r\n        addCategories() {\r\n            const apiUrl = 'http://nextcloud.local/index.php/apps/nextopn/api/firewall/categories/addItem';\r\n            const payload = {\r\n                category: {\r\n                    auto: this.categoryData.auto? 1 : 0,\r\n                    color: this.categoryData.color.trim().replace(/^#/, ''),\r\n                    name: this.categoryData.name.trim(),\r\n                },\r\n            };\r\n            console.log(\"Payload before sending: \", payload);\r\n\r\n            axios.post(apiUrl, payload, {\r\n                headers: {\r\n                    'Content-Type': 'application/json; charset=UTF-8', \r\n                },\r\n            })\r\n            .then(response => {\r\n                if(response.data.success) {\r\n                    this.message = response.data.message;\r\n                    this.updateKey++;\r\n                    this.fetchCategories();\r\n                } else {\r\n                    this.message = 'Error: ' + response.data.message;\r\n                }\r\n            })\r\n            .catch(error => {\r\n                this.message = 'Error: ' + (error.response && error.response.data.message || error.message);\r\n            });\r\n        },\r\n        fetchCategories() {\r\n            const apiUrl = OC.generateUrl('/apps/nextopn/api/firewall/categories/getCategories');\r\n            axios.get(apiUrl)\r\n                .then(response => {\r\n                    if (response.data && Array.isArray(response.data)) {\r\n                        this.categories = response.data;\r\n                    } else if (response.data.errorMessage) {\r\n                        console.error(response.data.errorMessage);\r\n                        // Handle error message\r\n                    }\r\n                })\r\n                .catch(error => {\r\n                console.error('Error fetching Categories:', error);\r\n                // Handle HTTP error\r\n                });\r\n        },\r\n        deletecategory(uuid) {\r\n            const apiUrl = OC.generateUrl(`/apps/nextopn/api/firewall/categories/delItem/${uuid}`);\r\n            axios.post(apiUrl)\r\n                .then(response => {\r\n                if(response.data.success) {\r\n                    this.message = response.data.message;\r\n                    this.updateKey++;\r\n                    this.fetchCategories();\r\n                    \r\n                } else {\r\n                    this.message = 'Error: ' + response.data.message;\r\n                }\r\n                })\r\n                .catch(error => {\r\n                    this.message = 'Error: ' + (error.response && error.response.data.message || error.message);\r\n                });\r\n        },\r\n        sortBy(key) {\r\n            if (this.sortKey === key) {\r\n            this.sortDirection = this.sortDirection * -1;\r\n            } else {\r\n            this.sortKey = key;\r\n            this.sortDirection = 1;\r\n            }\r\n        },\r\n        updateSortedCategories() {\r\n        // Update sortedAliases whenever aliases changes\r\n        this.categories = [...this.categories]; // Trigger Vue reactivity\r\n        },\r\n    },\r\n}\r\n</script>\r\n\r\n<style scoped>\r\nh1 {\r\n    font-size: 40px;\r\n    text-align: center;\r\n    margin-top: 0.5em;\r\n    margin-bottom: 0.5em;\r\n}\r\n#autoCheckBox {\r\n    height: 25px;\r\n    width: 25px;\r\n    background-color: #eee;\r\n}\r\n#main-panel {\r\n    margin: 1rem;\r\n    padding: 2rem 2rem;\r\n    width: 100%;\r\n}\r\n#form {\r\n    font-size: 15px;\r\n    position: relative;\r\n    text-align: center;\r\n}\r\n\r\nh2 {\r\n    position: relative;\r\n    text-align: center;\r\n    font-size: 22px;\r\n}\r\n.div-child {\r\n    display: block;\r\n    padding: 1rem 1rem;\r\n}\r\n#item-list {\r\n    position: relative;\r\n    width: 100%;\r\n    display: inline-table;\r\n    \r\n}\r\n#elements-list {\r\n    font-size: 20px;\r\n    display: block;\r\n    padding: 0.75rem 1rem;\r\n    border: solid 8px rgba(0, 0, 0, 0.25);\r\n    margin-bottom: 0.5rem;\r\n}\r\n.circle {\r\n    width: 20px;\r\n    height: 20px;\r\n    border-radius: 50%;\r\n    display: inline-block;\r\n    margin-left: 5px; /* Adjust the margin as needed */\r\n}\r\n\r\n.green {\r\n    background-color: rgb(43, 193, 43);\r\n}\r\n.red {\r\n    background-color: red;\r\n}\r\n.color-box {\r\n    border: 2px solid #000000;\r\n    width: 25px;\r\n    height: 25px;\r\n}\r\nth {\r\n    backdrop-filter: blur(10px);\r\n    position: sticky;\r\n    width: 10%;\r\n    cursor: pointer;\r\n}\r\n#Info-Panel {\r\n    border: 2px solid;\r\n    border-color: black;\r\n    padding: 0.5em;\r\n    border-radius: 2em;\r\n}\r\n#table-container {\r\n    margin: 2%;\r\n    height: 500px;\r\n    overflow: auto;\r\n}\r\n.table-content {\r\n    margin: 4%;\r\n  }\r\n/* elem {\r\n    width: 100%;\r\n    width: -moz-available;           WebKit-based browsers will ignore this.\r\n    width: -webkit-fill-available;   Mozilla-based browsers will ignore this. \r\n    width: fill-available;\r\n} */\r\n\r\n</style>"],"sourceRoot":""}]);
