.firmware-container {
    position: relative;
    overflow: auto;
    margin: auto;
    height: 1500px;
    width: 1100px;
}
.firmware-header {
    font-size: 50px;
    color: white;
    text-decoration: none;
    padding: 0.5rem 1rem;
    transition: color 0.3s ease;
    text-align: center;
}
.space-user {
    height: 50px;
}
.check-updates-button {
    position: absolute;
    bottom: 75%;  /* adjust these values */
    left: 50%; /* adjust these values */
    transform: translate(-50%, -50%); /* centers the button */
}
.update-info p {
    font-size: 25px;
    color: mintcream;
    padding: 0.5rem 1rem;
    transition: color 0.3s ease;
    text-align: center;
}
.product-info p {
    font-size: 25px;
    color: mintcream;
    padding: 0.5rem 1rem;
    transition: color 0.3s ease;
    text-align: center;
}
.error-message {
    color: red;
}
`, "",{"version":3,"sources":["webpack://./src/components/Firmware.vue"],"names":[],"mappings":";AA8EA;IACA,kBAAA;IACA,cAAA;IACA,YAAA;IACA,cAAA;IACA,aAAA;AACA;AAEA;IACA,eAAA;IACA,YAAA;IACA,qBAAA;IACA,oBAAA;IACA,2BAAA;IACA,kBAAA;AACA;AAEA;IACA,YAAA;AACA;AAEA;IACA,kBAAA;IACA,WAAA,GAAA,wBAAA;IACA,SAAA,EAAA,wBAAA;IACA,gCAAA,EAAA,uBAAA;AAEA;AAGA;IACA,eAAA;IACA,gBAAA;IACA,oBAAA;IACA,2BAAA;IACA,kBAAA;AACA;AAEA;IACA,eAAA;IACA,gBAAA;IACA,oBAAA;IACA,2BAAA;IACA,kBAAA;AACA;AAGA;IACA,UAAA;AACA","sourcesContent":["<template>\n    <div class=\"firmware-container\">\n        <h1 class=\"firmware-header\"> Firmware </h1>\n        <button class=\"check-updates-button\" @click=\"checkUpdates\">Update</button>\n\n        <div class=\"space-user\">\n\n        </div>\n\n        <div v-if=\"productID && productVersion\" class=\"product-info\">\n            <p><strong>Type:</strong> {{ productID }}</p>\n            <p><strong>Version:</strong> {{ productVersion }}</p>\n        </div>\n\n        <div v-if=\"updateInfo && updateInfo.statusMessage\" class=\"update-info\">\n            <p><strong>Status Message:</strong> {{ updateInfo.statusMessage }}</p>\n        </div>\n        <p v-if=\"errorMessage\" class=\"error-message\"> {{ errorMessage }}</p>\n    </div>\n</template>\n\n<script>\nimport axios from 'axios'; // Import Axios\n\nexport default {\n    data() {\n        return {\n            updateInfo: null,\n            errorMessage: '',\n            productID: '',\n            productVersion: '',\n        };\n    },\n    methods: {\n        async checkUpdates() {\n            try {\n                const response = await axios.get('http://nextcloud.local/index.php/apps/nextopn/api/firmware/status');\n                console.log(response); \n                if (response.data && response.data.statusMessage) {\n                    this.updateInfo = response.data;\n                } else {\n                    this.updateInfo = null;\n                    this.errorMessage = 'No updates available.';\n                }\n            } catch (error) {\n                this.updateInfo = null;\n                this.errorMessage = 'Error fetching update information.';\n                console.error(error);\n            }\n        },\n\n        async getInfoMethod() {\n            try {\n                const response = await axios.get('http://nextcloud.local/index.php/apps/nextopn/api/firmware/info');\n                console.log(response);\n\n                if (response.data.productID && response.data.productVersion) {\n                    this.productID = response.data.productID;\n                    this.productVersion = response.data.productVersion;\n                } else {\n                    this.errorMessage = \"No system information available.\"\n                }\n            } catch {\n                this.errorMessage = \"Error fetching system information.\"\n            }\n        }\n\n    },\n\n    created() {\n        this.getInfoMethod();\n        this.checkUpdates();\n    }\n\n};\n</script>\n\n<style>\n .firmware-container {\n    position: relative;\n    overflow: auto;\n    margin: auto;\n    height: 1500px;\n    width: 1100px;\n }\n\n .firmware-header {\n    font-size: 50px;\n    color: white;\n    text-decoration: none;\n    padding: 0.5rem 1rem;\n    transition: color 0.3s ease;\n    text-align: center;\n }\n\n .space-user {\n    height: 50px;\n }\n\n .check-updates-button {\n    position: absolute;\n    bottom: 75%;  /* adjust these values */\n    left: 50%; /* adjust these values */\n    transform: translate(-50%, -50%); /* centers the button */\n    \n}\n\n\n.update-info p {\n    font-size: 25px;\n    color: mintcream;\n    padding: 0.5rem 1rem;\n    transition: color 0.3s ease;\n    text-align: center;\n}\n\n.product-info p {\n    font-size: 25px;\n    color: mintcream;\n    padding: 0.5rem 1rem;\n    transition: color 0.3s ease;\n    text-align: center;\n}\n\n\n.error-message {\n    color: red;\n}\n</style>"],"sourceRoot":""}]);
